.item-container{
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.16);
    background-color: #f2eded;
    margin-left: 25%;
    margin-bottom: 20px;
    border-radius: 25px;
    border-bottom-left-radius: 85px;
    border-top-left-radius: 15px;
    border-top-right-radius: 85px;
}

.item-container:hover{
    background-color: rgb(139, 216, 24);
    font-weight: 500;
}

.rest-image{
    height: 160px;
    width: 200px;
    border-radius: 15%;
    margin-top: 35px;
    margin-right: 18px;
    margin-left: 5px;
    float: left;
}

.content{
    margin-right: 5px;
    margin-top: 35px;
    display: block;
}

.hotel_name{
    font-size: 35px;
    color:  #3d6bcf;
}

.city_name{
    font-size: 15px;
}

.cuisine{
    color: rgb(206, 70, 21);
    font-size: 28px;
    margin-top: 38px;
}

.price-container{
    margin-right: 25px;
    float: left;
    width: 175px;
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    color: rgb(93, 240, 240);
    
}

.price-container strong{
    font-size: 50px;
}

.price-container strong:hover{
    font-size: 70px;
}

@media only screen and (width:500px) {
    .item-container{
        width: 240px;
        height: 220px;
        box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.16);
        background-color: #f2eded;
        margin-bottom: 20px;
        margin-left: 25px;
        border-radius: 25px;
        border-bottom-left-radius: 85px;
        border-top-left-radius: 15px;
        border-top-right-radius: 85px;
    }
    
    .item-container:hover{
        background-color: rgb(139, 216, 24);
        font-weight: 500;
    }
    
    .rest-image{
        height: 40px;
        width: 40px;
        border-radius: 15%;
        margin-top: 35px;
        margin-right: 28px;
        float: left;
    }
    
    .content{
        margin-right: 3px;
        margin-top: 10px;
        float: left;
        display: block;
    }
    
    .hotel_name{
        font-size: 18px;
        color:  #3d6bcf;
    }
    
    .city_name{
        font-size: 12px;
    }
    
    .cuisine{
        color: rgb(206, 70, 21);
        font-size: 15px;
        margin-top: 20px;
    }
    
    .price-container{
        float: left;
        width: 175px;
        text-align: center;
        font-size: 18px;
        font-weight: 600;
        color: rgb(93, 240, 240);
        
    }
    
    .price-container span{
        font-size: 30px;
    }
    
    .price-container span:hover{
        font-size: 35px;
    }
}

@media only screen and (max-width: 650px) {
    .item-container{
        width: 450px;
        height: 300px;
        box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.16);
        background-color: #f2eded;
        margin-bottom: 20px;
        margin-left: 25px;
        border-radius: 25px;
        border-bottom-left-radius: 85px;
        border-top-left-radius: 15px;
        border-top-right-radius: 85px;
    }
    
    .item-container:hover{
        background-color: rgb(139, 216, 24);
        font-weight: 500;
    }
    
    .rest-image{
        height: 90px;
        width: 95px;
        border-radius: 25%;
        margin-top: 35px;
        float: left;
    }
    
    .content{
        margin-right: 8px;
        margin-top: 20px;
        float: left;
        display: block;
    }
    
    .hotel_name{
        font-size: 22px;
        color:  #3d6bcf;
    }
    
    .city_name{
        font-size: 15px;
    }
    
    .cuisine{
        color: rgb(206, 70, 21);
        font-size:25px;
        margin-top: 20px;
    }
    
    .price-container{
        float: left;
        width: 175px;
        text-align: center;
        font-size: 18px;
        font-weight: 600;
        color: rgb(93, 240, 240);
        
    }
    
    .price-container span{
        font-size: 40px;
    }
    
    .price-container span:hover{
        font-size: 45px;
    }
}