.App {
  text-align: center;
  max-width: 500px;
  margin: 0 auto;
}

.App section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgb(40, 37, 53);
}

.heading{
  color: white;
}

.sign-out{
  margin-left: 295px;
}

.message-box {
  padding: 10px;
  height: 40vh;
  margin: 10vh 0 10vh;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}


.input-box{
  height: 10vh;
  bottom: 0;
  background-color: rgb(24, 23, 23);
  width: 100%;
  max-width: 728px;
  display: flex;
  font-size: 1.5rem;
}

.input-box button {
  width: 18%;
  background-color: rgb(56, 56, 143);
}


.typehere {
  line-height: 1;
  width: 100%;
  font-size: 2rem;
  background: rgb(131, 124, 124);
  color: white;
  padding: 0 5px;
}

.send-button {
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  display: inline-block;
  cursor: pointer;
  font-size:  2.3rem;
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}


.sign-in {
  color: #282c34;
  background: white;
  max-width: 400px;
  margin: 0 auto;
}

.msg-info{
  color: aqua;
  
}


ul, li {
  text-align: left;
  list-style: none;
}

.show-text {
  max-width: 300px;
  margin-bottom: 10px;
  line-height: 10px;
  padding: 8px 15px;
  border-radius: 10px;
  position: relative;
  color: white;
  text-align: center;
}

.message {
  display: flex;
  align-items: center;
}


.sent {
  flex-direction: row-reverse;
}

.sent p {
  color: white;
  background: #0b93f6;
  align-self: flex-end;
}
.received p {
  background: #e5e5ea;
  color: black;
}

.pic{
  height: 50px;
  width: 50px;
  border-radius: 50%;
}
