.social-icons{
    width: 50px;
    position: fixed;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    z-index: 1;
}
.social-icons img{
    display: block;
    width: 20px;
    margin: 0 auto 20px;
    transition: 0.5s;
}

.social-icons img:hover{
    width: 50px;
}

#sidebtn li Link:hover{
    font-size: 20px;
}